body {
    margin: 0;
    padding: 0;
    background: #f5f5f5;
}

#root {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 5% 0;
}

canvas {
    border: 1px solid rgba(0, 0, 0, .075);
    border-radius: 5px;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, .025);
    overflow: hidden;
    margin: 5px;
    width: fit-content;
    background: white;
}